/* ------------------------------------------<0: VARIABLES>------------------------------------------ */

/* particles.js variables */
:root {
  --amount-of-particles: 15;
  --density-of-particles: 100;
  --type-of-particles: "circle";
  --particle-opacity: 0.1;
  --particle-size: 7.5;
  --particle-speed: 2.6;
  --particle-direction: "none";
  --amount-of-lines: 400;
  --line-opacity: 0;
  --line-width: 0.5;
}

