/* ------------------------------------------<1: SETUP>------------------------------------------ */
html {
  overflow-y: scroll;
  overflow-x: hidden;
}

body {
  font-family: 'Karla', sans-serif !important;
  padding: 0;
  margin: 0;
  background: linear-gradient(
      180deg,
      var(--bg1),
      var(--bg2)
  );
  background-size: 400%;
  animation: animation_name 55s ease infinite;    /* lasts 30s, eases in, and is infinite */
}

@keyframes animation_name {
  /*                          (x,  y) */
  0% { background-position:   0%   0%; }     /* x=top left, y=centre */
  50% { background-position:  100% 50%; }     /* x=bottom left, y=centre */
  100% { background-position: 0%   100%; }     /* x=top left, y=centre */
}

#particles-js canvas{
  position: fixed;
  width: 100%;
  height: 100vh;
}
/* only put the core elements on top of #particles-js, not the entire page because it removes the interactivity */
.portfolio p, .portfolio a, .portfolio h2, .portfolio h1, .portfolio h3, .portfolio h4, .portfolio h5, .portfolio h6, .portfolio .project, .my-navbar {
  z-index: 1;
  position: relative;
}

.center-me {
  text-align: center;
}

a:not([href]):not([class]) {
  color: rgb(0, 123, 255) !important;
  background-color: transparent !important;
}
a:not([href]):not([class]):hover {
  text-decoration: underline !important;
  color: rgb(2, 88, 181) !important;
  cursor: pointer;
}

/* ------------------------------------------<2: NAVBAR (includes top and side navigation)>------------------------------------------ */
.my-navbar {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  top: 0;
  left: 0;
}
.my-navbar ul {
  margin: 0;
}
.my-navbar ul .left-nav {
  display: inline;
  z-index: 11111;
}
.my-navbar ul a {
  list-style: none;
  text-decoration: none;
  display: inline-block;
  color: var(--nav-item-text) !important;
  font-weight: 500;
}
.my-navbar ul a.active {
  color: var(--nav-item-text-active) !important;
}
.my-navbar ul a.active li {
  background-color: var(--nav-item-bg-active);
}
.my-navbar ul a:hover {
  text-decoration: none;
  cursor: pointer;
}
.my-navbar ul a li {
  text-transform: uppercase;
  padding: 0.3em;
  margin: 0 0.4em;
  border-radius: 12px;
}
.my-navbar ul a li:hover {
  background-color: var(--nav-item-bg-hover);
}
.my-navbar ul .delimiter {
  font-family: Arial,sans-serif;
  font-weight: normal;
  letter-spacing: 0px;
  font-size: 14px;
  font-weight: 500;
  font-style: normal;
  letter-spacing: .1em;
  text-transform: uppercase;
  color: var(--nav-delimiter);
  padding-left: 0.1em;
  padding-right: 0em;
}

.my-navbar div.extra-menu-btn {
  border-radius: 50px;
  padding: 10px;
  margin: -10px 12px;
  display: inline-block;
  transition: background-color 50ms ease-in;
  opacity: 0.5;
}
.my-navbar div.extra-menu-btn:hover {
  cursor: pointer;
  opacity: 1.0;
  background-color: rgb(245, 245, 245);
}
.my-navbar div.extra-menu-btn:active {
  background-color: rgb(223, 223, 223);
}
.my-navbar ul.extra-menu {
  display: none;
  width: auto;
  padding: 5px;
  position: absolute;
  left: 620px;
  top: 75px;
  border: 1px solid var(--nav-item-theme-toggle-border-color-active);
  background-color: var(--bg1);
}
.my-navbar ul.extra-menu.active {
  display: block;
}
.my-navbar ul.extra-menu.active li {
  list-style: none;
  display: block;
}
.my-navbar ul.extra-menu.active li:hover {
  background-color: var(--nav-item-bg-hover);
}


.theme-toggle {
  color: var(--nav-item-theme-toggle-color);
  border: 1px solid transparent;
  background-color: transparent;
  height: 42px;
  border-radius: 5px;
  cursor: pointer;
  transition: 150ms ease;
  display: inline-block;
  right: 20px;
  top: 20px;
  position: absolute;
}
.theme-toggle:hover {
  color: var(--nav-item-theme-toggle-color-hover);
}
.theme-toggle:active {
  border: 1px solid var(--nav-item-theme-toggle-border-color-active);
}
.theme-toggle .inline-svg {
  width: 2.6em;
}
.inline-svg {
  width: 1em;
  display: block;
}
.inline-svg[hidden] {
  display: none;
}
svg:not(:root) {
  overflow: hidden;
}

/* ---------------< Logo >--------------- */
/* add padding to the logo's link container */
.my-navbar .logo-container {
  padding: 1.2em 1.7em;
}

svg.logo {
  width: 50px;
  height: 50px;
  stroke: rgb(26, 9, 39);
  stroke-width: 0.1px;
  stroke-opacity: 0.11;
  z-index: 10;
  fill-opacity: 1;
}
svg.logo path:nth-of-type(3n+0) {  fill: rgb(21, 124, 47); }
svg.logo path:nth-of-type(3n+1) {  fill: rgb(93, 218, 114); }
svg.logo path:nth-of-type(3n+2) {  fill: rgba(7, 7, 7, 0); }

svg.logo path:hover ~ * {
  fill-opacity: 0.1;
  fill: rgba(17, 14, 221, 0.234);
  stroke-width: 5.1px;
}
svg.logo:active path {
  fill: rgb(20, 35, 49);
  transition: all 350ms ease;
}


svg.logo polygon:hover {
  cursor: pointer;
  fill-opacity: 0.50;
}

svg.logo polygon.a { fill: rgb(177, 170, 89); fill-opacity: 1; }
svg.logo polygon.b { fill: rgb(255, 239, 69); fill-opacity: 0.84; }
svg.logo polygon.c { fill: rgb(255, 192, 203); fill-opacity: 0.28;  }
svg.logo polygon.d { fill: rgb(193, 208, 96); fill-opacity: 0.64; }
svg.logo polygon.e { fill: rgb(36, 94, 170); fill-opacity: 0.41; }
svg.logo polygon.f { fill: rgb(63, 67, 31); fill-opacity: 0.29; }

svg.logo polygon:hover { 
  cursor: pointer;
  fill-opacity: 0.49;
  fill: rgb(67, 177, 175);
}

.logo-container span {
	font-family: Varela Round,Helvetica,arial,sans-serif;
	font-weight: normal;
	text-transform: normal;
	letter-spacing: 0px;
	font-family: brandon-grotesque;
	font-weight: 200;
	font-style: normal;
	line-height: 1.2em;
	text-transform: none;
	letter-spacing: .08em;
  font-size: 26px;
  color: var(--text) !important;
}


