.hamburger-menu-icon {
  background: transparent;
  border: none;
  cursor: pointer;
  display: none;
  outline: none;
  height: 30px;
  position: relative;
  width: 30px;
  z-index: 1000;
  position: absolute;
  top: 27px;
  right: 100px;
}
.hamburger-menu-icon:active {
  border: none;
  outline: none;
}
.hamburger-menu-icon span.hamburger-line {
  background: var(--blog-post-title-text);
  height: 3px;
  position: absolute;
  left: 0;
  transition: all .2s ease-out; 
  width: 100%;
}
.hamburger-menu-icon:hover {
  
}
span.hamburger-line-top { top: 3px; }
span.hamburger-line-bottom { bottom: 3px; }
span.hamburger-line-middle { top: 50%; transform: translateY(-50%); }



/* ------------------------------------------<3: MAIN CONTAINER>------------------------------------------ */
.page-wrapper {
  width: 100vw;
  top: 0;
  margin-top: -210px;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.page-wrapper.university,
.page-wrapper.universityAssignment,
.page-wrapper.blog,
.page-wrapper.blog-post,
.page-wrapper.notes {
  background-color: var(--page-wrapper-bg) !important;
  padding-top: 14rem;
}

.page-wrapper.university,
.page-wrapper.blog,
.page-wrapper.notes {
  height: 111vh;
  padding-left: 6rem;
  padding-right: 6rem;
}

.page-title {
  color: var(--page-text-color);
  text-align: center;
  font-size: 3.15rem !important;
  font-weight: bold !important;
  margin-bottom: 50px !important;
}

*:focus {
  outline: 0;
  outline: none;
}


/* ------------------------------------------<4: PORTFOLIO>------------------------------------------ */
.portfolio {
  top: 0;
  color: var(--intro-text);
  height: 270vh;
  padding-top: 100px;
}

.portfolio p {
  font-size: 1.2em;
	line-height: 1.4em;
	letter-spacing: -0.014em;
	font-feature-settings: "ss03","zero","cv08","cv11" 1;
  text-transform: none;
}
.portfolio h1 {
  font-size: 6vw;
  line-height: 0.9em;
  margin: 0.2em 0 0.6em;
  font-weight: 900;
}
.portfolio h2 {
  font-size: 4vw;
  font-weight: 900;
  margin: 0.2em 0 0.6em;
}

.portfolio #about, 
.portfolio #projects,
.portfolio #contact {
  height: 100vh;
  padding: 0 25px;
}

#about, .projects-header {
  padding-left: 200px;
  transition: padding-left 0.3s ease-out;
}

/* the arrow button that takes to an area on the same page */
.smooth-scroll-btn {
  width: 100%;
  margin-top: 1.8em;
}
.smooth-scroll-btn a i {
  font-size: 5rem;
  cursor: pointer;
  color: var(--arrow-smooth-scroll-btn);
}
.smooth-scroll-btn a i:hover {
  color: var(--contact-btn-colour-hover);
}

/* --------------<#about SECTION> -------------- */
#about {
  padding: 170px 0px 0px 200px !important;
  display: block;
  top: auto;
	right: auto;
	bottom: auto;
  max-width: 1500px;
  margin-top: 100px;
}

#about img {
  object-fit: cover;
  object-position: 100% 100%; /* positioned top left of the content box */
  width: 250px;
  height: 250px;
  border-radius: 50%;
  filter: drop-shadow(0 0 15px #000);
}

@media screen and (max-width: 900px) {
  #about img {
    width: 230px;
    height: 230px;
    transition: 0.3s all;
  }
}

/* The contact buttons */
.contact {
  display: block;
  margin: 0 auto;
  text-align: center;
  margin-top: 20px;
}

.contact i {
  font-size: 50px;
  color: var(--contact-btn-colour);
}

.contact a {
  cursor: pointer;
  text-decoration: none;
  padding: 20px;    /* space out the contact icons */
}

.contact .cv .contact-btn {
  background-color: var(--contact-btn-colour);
  border: 1px solid var(--contact-btn-border-colour);
  color: var(--contact-btn-font-colour);
  opacity: 0.7;
  font-size: 20px;
  border-radius: 20px;
  padding: 20px;
  display: inline-block;
}

.contact i:hover {
  color: var(--contact-btn-colour-hover);
  transition: 0.3s;
}
.contact-btn:hover {
  background-color: var(--contact-btn-colour-hover) !important;
  transition: 0.3s;
  color: var(--bg1);
}

/* --------------<#projects SECTION> -------------- */
#projects {
  padding-top: 0px;
}

.projects-container {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-template-rows: repeat(3, 1fr);
  padding: 0 120px;
}

/* Media queries for the project images */
@media screen and (min-width: 1910px) { .project img { width: 18.5vw; } }
@media screen and (max-width: 1900px) { .project img { width: 15.9vw; } }
@media screen and (max-width: 1879px) { .project img { width: 18.9vw; } }
@media screen and (max-width: 1595px) { .project img { width: 24.1vw; } }
@media screen and (max-width: 1282px) { .project img { width: 32.1vw; } }
@media screen and (max-width: 969px) { .project img { width: 48.1vw; } }
@media screen and (max-width: 656px) { .project img { width: 97.1vw; } }
