.project {
  background-color: var(--project-card-bg) !important;
  border: 1px solid var(--project-card-bg) !important;
  box-shadow: 0 1px 3px 0 var(--project-card-shadow),0 0 0 1px var(--project-card-bg) !important;
  text-align: left;
}

.project, .image {
  border-radius: 10px;
}

.project a {
  text-decoration: none;
}

.project img {
  height: 29vh;
}

.project * {
  color: var(--project-card-text-color);
}

.cropped {
  height: 15vh !important;
  object-fit: cover;
}

.image {
  cursor: pointer;
  overflow: hidden;
  border: 1px solid var(--project-card-bg);
}

.proj-title {
